<script>
  import { tick, onMount } from 'svelte';
  import { mapTabDetail } from '$lib/mapper.js';
  // export let tab;
  export let id;
  export let tabs = [];
  export let activeTabId;

  $: activeTabId = mapTabDetail[activeTabId] || activeTabId;

  // Dùng Map thay vì object để tiện mở rộng động
  /**
  // Thêm component động
  registerComponent('invoice', () => import('./InvoiceDetail.svelte'));

  // Load (có cache)
  const InvoiceComp = await loadComponent('invoice');

  // Xoá component và cache
  unregisterComponent('invoice');
 */
  const componentMap = new Map([
    // ['user', () => import('./UserDetail.svelte')],
    // ['report', () => import('./ReportView.svelte')],
    // ['product', () => import('./ProductDetail.svelte')],
    // ['result', () => import('./ResultDetail.svelte')]
  ]);

  // Hàm đăng ký động
  export function registerComponent(type, loaderFn) {
    componentMap.set(type, loaderFn);
  }

  // Hàm xoá component khỏi map (nếu cần)
  export function unregisterComponent(type) {
    componentMap.delete(type);
  }

  // Huỷ đăng ký component + clear cache
  export function unregisterComponentCache(type) {
    componentMap.delete(type);
    loadedComponents.delete(type);
  }

  // Cache component đã load
  let loadedComponents = new Map();
  let currentComponent = null;
  let isLoading = false;
  let error = null;

  // Reactive: chỉ load khi activeTabId thay đổi
  $: if (activeTabId) {
    loadComponent(activeTabId);
  }

  export async function loadComponentCache(type) {
    if (loadedComponents.has(type)) {
      return loadedComponents.get(type);
    }

    const loader = componentMap.get(type);
    if (!loader) {
      throw new Error(`Component loader for type "${type}" not found`);
    }

    const module = await loader();
    const comp = module.default;
    loadedComponents.set(type, comp);
    return comp;
  }

  // Hàm load component theo type
  async function loadComponent(type) {
    if (!type) return;
    if (currentComponent && loadedComponents.has(type)) {
      // Đã load rồi thì gán luôn
      currentComponent = loadedComponents.get(type);
      return;
    }

    isLoading = true;
    error = null;
    currentComponent = null;

    try {
      const loader = componentMap.get(type);
      if (typeof loader !== 'function') {
        throw new Error(`Component loader for type "${type}" not found`);
      }

      // Nếu cache có thì lấy luôn
      if (loadedComponents.has(type)) {
        currentComponent = loadedComponents.get(type);
      } else {
        const module = await loader();
        currentComponent = module.default;
        loadedComponents.set(type, currentComponent);
      }
    } catch (e) {
      error = e;
    } finally {
      isLoading = false;
      await tick();
    }
  }

  // Cache loader động
  // const componentMap = new Map();
  // const loadedComponents = new Map();

  /**
   * Đăng ký component theo tên string (vd: "InvoiceDetail")
   * @param {string} name
   * // Gọi trực tiếp
    const Comp = await loadComponentByName('InvoiceDetail');

    // Hoặc đăng ký trước rồi dùng
    registerComponentByName('InvoiceDetail');
    const Comp2 = await loadComponentByName('InvoiceDetail');
   */
  export function registerComponentByName(name) {
    // Tạo loader function động
    const loaderFn = () => import(`./${name}.svelte`);
    componentMap.set(name.toLowerCase(), loaderFn);
  }

  /**
   * Load component theo tên (vd: "InvoiceDetail" hoặc "invoiceDetail")
   * @param {string} name
   */
  export async function loadComponentByName(name) {
    const key = name.toLowerCase();

    if (loadedComponents.has(key)) {
      return loadedComponents.get(key);
    }

    let loader = componentMap.get(key);
    if (!loader) {
      // Nếu chưa đăng ký thì tự tạo loader theo tên file
      loader = () => import(`./${name}.svelte`);
      componentMap.set(key, loader);
    }

    const module = await loader();
    const comp = module.default;
    loadedComponents.set(key, comp);
    return comp;
  }

  // preload nhiều component theo danh sách string
  export async function preloadComponents(names = []) {
    const results = {};
    for (const name of names) {
      try {
        const comp = await loadComponentByName(name);
        results[name] = comp;
      } catch (err) {
        console.error(`❌ Lỗi preload ${name}:`, err);
      }
    }
    return results;
  }

  // preload trước khi app chạy
  onMount(async () => {
    await preloadComponents(['HomeDetail', 'ResultDetail']);
    //console.log('✅ Preloaded HomeDetail and ResultDetail', loadedComponents);
  });
</script>

<slot {currentComponent} {tabs} {activeTabId} {isLoading} {error} {id} />
